body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #222;
  color: #fff;
  display: flex;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.movie {
  width: 300px;
  margin: 0.5rem;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  background: #111;
  overflow: hidden;
  position: relative;
}

.movie img {
  max-width: 100%;
}

.App {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 2rem 0;
}

.movie-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.logo {
  padding-right: 0;
  height: 30px;
  width: auto;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

.container {
  display: table;
  margin: 0 auto;
  padding: 15px;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.movie-info h3 {
  margin: 0;
}
.movie-info span {
  font-size: 85%;
  font-family: Helvetica;
}

.movie-over {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: white;
  color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out;
}

.movie:hover {
  cursor: pointer;
}

.movie:hover .movie-over {
  transform: translateY(0);
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #111;
  padding: 0 0.75rem;
}

.search {
  font-family: inherit;
  font-size: 1rem;
  border: 2px solid #222;
  border-radius: 50px;
  padding: 0.3rem 1rem;
  background-color: transparent;
  color: #fff;
}

.search:focus {
  outline: none;
}

.tag {
  background-color: #333;
  color: white;
  padding: 0.5rem;
  border-radius: 5px;
}

.tag.green {
  color: rgb(29, 199, 29);
}

.tag.yellow {
  color: rgb(255, 174, 24);
}

.tag.red {
  color: rgb(255, 45, 45);
}
